import { mapState } from 'vuex'
export default{
	components:{
		
	},
	data(){
		return {

		}
	},
	computed:{
		...mapState(['userInfo'])
	},
	methods:{
		// 更换头像
		avatarImgChange(){
			uni.chooseImage({
			    count: 1, 
			    success: res => {
					this.userInfo.avatarImg = res.tempFiles[0].path
			        this.$store.commit('setUserInfo',this.userInfo)
			    }
			});
		},
		//去编辑
		editInfo(){
			uni.navigateTo({
				url:'/sub_user/pages/personalInfo/info-edit'
			})
		}
	}
}
